<template>
  <div id="app">
    <h1>My To-Do List</h1>
    <to-do-form @todo-added="addToDo"></to-do-form>
    <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
    <ul aria-labelledby="list-summary" class="stack-large">
      <draggable v-model="ToDoItems" @end="onDragEnd">
        <transition-group>
          <li v-for="item in ToDoItems" :key="item.id">
            <el-card>
              <to-do-item
                :label="item.label"
                :done="item.done"
                :id="item.id"
                @checkbox-changed="updateDoneStatus(item.id)"
                @item-deleted="deleteToDo(item.id)"
                @item-edited="editToDo(item.id, $event)">
              </to-do-item>
            </el-card>
          </li>
        </transition-group>
      </draggable>
    </ul>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="ToDoItems.length">
    </el-pagination>
    <el-select v-model="selectedTag" placeholder="Filter by tag">
      <el-option v-for="tag in availableTags" :key="tag" :label="tag" :value="tag"></el-option>
    </el-select>
    <div class="demo-rate-block">
      <span class="demonstration">Default</span>
      <el-rate v-model="value1" />
    </div>
    <div class="demo-rate-block">
      <span class="demonstration">Color for different levels</span>
      <el-rate v-model="value2" :colors="colors" />
    </div>

  </div>
</template>


<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm.vue";

export default {
  name: "app",
  components: {
    ToDoItem,
    ToDoForm,
  },
  data() {
  return {
    ToDoItems: [
      { id: uniqueId("todo-"), label: "Learn Vue", done: false },
      { id: uniqueId("todo-"), label: "Create a Vue project with the CLI", done: false },
      { id: uniqueId("todo-"), label: "Have fun", done: false },
      { id: uniqueId("todo-"), label: "Create a to-do list", done: false },
    ],
    currentPage: 1,
    pageSize: 10,
    value1: 0,
      value2: 0,
      colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
  };
},

  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({ id: uniqueId('todo-'), label: toDoLabel, done: false });
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToUpdate.done = !toDoToUpdate.done;
    }, 
    deleteToDo(toDoId) {
    const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
    this.ToDoItems.splice(itemIndex, 1);
    this.$refs.listSummary.focus();
},
editToDo(toDoId, newLabel) {
  const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
  toDoToEdit.label = newLabel;
},
handleSizeChange(val) {
    this.pageSize = val;
  },
  handleCurrentChange(val) {
    this.currentPage = val;
  },
  onDragEnd() {
    // 处理拖拽结束后的逻辑，例如更新待办事项的顺序
  },
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) => item.done).length;
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`;
    },
    paginatedToDoItems() {
    const start = (this.currentPage - 1) * this.pageSize;
    const end = this.currentPage * this.pageSize;
    return this.ToDoItems.slice(start, end);
  },
  filteredToDoItems() {
    if (this.selectedTag) {
      return this.ToDoItems.filter(item => item.tags.includes(this.selectedTag));
    }
    return this.ToDoItems;
  },
  },
 
}
</script>


