<template>
  <el-form @submit.prevent="onSubmit">
    <h2 class="label-wrapper">
      <label for="new-todo-input" class="label__lg">
        What needs to be done?
      </label>
    </h2>
    <el-input
      type="text"
      id="new-todo-input"
      name="new-todo"
      autocomplete="off"
      v-model.lazy.trim="label"
      class="input__lg"
    />
    <el-button type="primary" round @click="onSubmit">Add</el-button>
    <el-alert
      v-if="showError"
      type="error"
      title="Error"
      description="The to-do item cannot be empty."
      show-icon>
    </el-alert>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      label: "",
      showError: false,
    };
  },
  methods: {
    onSubmit() {
      if (this.label === "") {
        this.showError = true;
        return;
      }
      this.$emit('todo-added', this.label);
      this.label = "";
      this.showError = false;
    },
  },
};
</script>
